@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap'); /*Import da fonte roboto*/

* {
    box-sizing: border-box; /*Tamanho da pagina em box*/
}

body{
    background-color: #111; /*Cor do fundo*/
    color: #fff; /*Cor da fonte*/
    margin: 0; /*Margem zerada da pagina*/
    font-family: 'Roboto', sans-serif; /*Fonte escolhida*/
}

.lists{
    margin-top: -150px;
}

footer{
    margin: 50px 0;
    text-align: center;
}

.loading{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}