header{
    position: fixed;/*Posição fixa, para nao empurrar os outros elementos*/
    z-index: 999;/*Para sempre ficar acima dos outros elementos*/
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;/*Para os itens se distribuirem entre os lados (um na esquerda e outro na direita)*/
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}
header.black{
    background-color: #141414;
}


.header--logo{
    height: 25px;/*Espaço permitido*/
}

.header--logo img{
    height: 100%;/*100% da altura do espaço disponivel*/
}

.header--user{
    height: 35px;
}

.header--user img{
    height: 100%;
    border-radius: 3px;
}