.featured{
    height: 100vh; /*Vai possuir todo o tamanho do monitor*/
}
.featured--vertical{
    width: inherit;/*Tamanho herdado do anterior (fetured)*/
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);/*Degrade de cima pra baixo, do preto para o transparente*/
} 
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);/*Degrade da direita para a esquerda, do preto para o transparente*/
    display: flex;
    flex-direction: column;
    justify-content: center;/*Aloca no meio do espaço disponível*/
    padding-left: 30px;/*Ajusta o distaciamneto em relação a esquerda*/
    padding-bottom: 150px;/*Ajusta o distanciamento em relação ao baixo*/
    padding-top: 70px;/*Ajusta o distanciamento em ralação ao topo*/
}

.featured--name{
    font-size: 60px;/*Tamanho da fonte*/
    font-weight: bold;/*Deixa em negrito*/
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;/*Margem em relação ao topo*/
}

.featured--points, .featured--year, .featured--seasons{
    display: inline-block;/*Coloca um ao lado do outro em blocos*/
    margin-right: 15px;/*Margem em relação a direita*/
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;/*Maior tamanho de largura*/
}

.featured--buttons{
    margin-top: 15px;
}

.featured--watchButton, .featured--mylistButton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;/*Tamanho de 15px em cima e baixo, 25px nos lados*/
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}
.featured--watchButton:hover, .featured--mylistButton:hover{
    opacity: 0.7;
}

.featured--watchButton{
    background-color: #FFF;
    color: #000;
}

.featured--mylistButton{
    background-color: #333;
    color: #FFF;
}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .featured{
        height: 90vh; /*Tamanho que será usado da tela do movel*/
    }

    .featured--name{
        font-size: 40px;
    }
    .featured--info{
        font-size: 16px;
    }
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchButton, .featured--mylistButton{
        font-size: 16px;
    }
    .featured--genres{
        font-size: 14px;
    }
}