@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
/*Import da fonte roboto*/

* {
    box-sizing: border-box; /*Tamanho da pagina em box*/
}

body{
    background-color: #111; /*Cor do fundo*/
    color: #fff; /*Cor da fonte*/
    margin: 0; /*Margem zerada da pagina*/
    font-family: 'Roboto', sans-serif; /*Fonte escolhida*/
}

.lists{
    margin-top: -150px;
}

footer{
    margin: 50px 0;
    text-align: center;
}

.loading{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}
.movieRow{
    margin-bottom: 30px; /*Ajusta a margem de baixo de uma linha pra outra */
}
.movieRow h2{
    margin: 0px 0px 0px 30px; /*Ajusta a margem de baixo*/
}
.movieRow--listarea{
    overflow-x: hidden;/*Aqueles que ultrapasssarem o eixo X da tela serão cortados*/
    padding-left: 30px;
}
.movieRow--list{
    transition: all ease 0.3s;
}
.movieRow--item{
    display: inline-block;/*Organiza os elementos em uma linha só*/
    width: 150px;/*Define um tamanho fixo*/
    cursor: pointer;
}
.movieRow--item img{
    width: 100%;/*Utiliza 100% do tamanho fixo definido*/
    -webkit-transform: scale(0.9);
            transform: scale(0.9);/*Reduz a escala dos elementos para 90%(0.9)*/
    transition: all ease 0.3s;/*A transição para o hover sera do tipo ease*/
}
.movieRow--item img:hover{/*Enquanto o mouse esta por cima da imagem*/
    -webkit-transform: scale(1);
            transform: scale(1);/*A escala volta a 100%*/
}

.movieRow--left, .movieRow--right{
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.4s;
    border-radius: 10px;
}

.movieRow--left{
    left: 0;
}

.movieRow--right{
    right: 0;
}

.movieRow:hover .movieRow--left, .movieRow:hover .movieRow--right{
    opacity: 1;
}

@media (max-width:760px){
    .movieRow--left, .movieRow--right{
        opacity: 1;
    }
}
.featured{
    height: 100vh; /*Vai possuir todo o tamanho do monitor*/
}
.featured--vertical{
    width: inherit;/*Tamanho herdado do anterior (fetured)*/
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);/*Degrade de cima pra baixo, do preto para o transparente*/
} 
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);/*Degrade da direita para a esquerda, do preto para o transparente*/
    display: flex;
    flex-direction: column;
    justify-content: center;/*Aloca no meio do espaço disponível*/
    padding-left: 30px;/*Ajusta o distaciamneto em relação a esquerda*/
    padding-bottom: 150px;/*Ajusta o distanciamento em relação ao baixo*/
    padding-top: 70px;/*Ajusta o distanciamento em ralação ao topo*/
}

.featured--name{
    font-size: 60px;/*Tamanho da fonte*/
    font-weight: bold;/*Deixa em negrito*/
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;/*Margem em relação ao topo*/
}

.featured--points, .featured--year, .featured--seasons{
    display: inline-block;/*Coloca um ao lado do outro em blocos*/
    margin-right: 15px;/*Margem em relação a direita*/
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;/*Maior tamanho de largura*/
}

.featured--buttons{
    margin-top: 15px;
}

.featured--watchButton, .featured--mylistButton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;/*Tamanho de 15px em cima e baixo, 25px nos lados*/
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}
.featured--watchButton:hover, .featured--mylistButton:hover{
    opacity: 0.7;
}

.featured--watchButton{
    background-color: #FFF;
    color: #000;
}

.featured--mylistButton{
    background-color: #333;
    color: #FFF;
}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .featured{
        height: 90vh; /*Tamanho que será usado da tela do movel*/
    }

    .featured--name{
        font-size: 40px;
    }
    .featured--info{
        font-size: 16px;
    }
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchButton, .featured--mylistButton{
        font-size: 16px;
    }
    .featured--genres{
        font-size: 14px;
    }
}
header{
    position: fixed;/*Posição fixa, para nao empurrar os outros elementos*/
    z-index: 999;/*Para sempre ficar acima dos outros elementos*/
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;/*Para os itens se distribuirem entre os lados (um na esquerda e outro na direita)*/
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}
header.black{
    background-color: #141414;
}


.header--logo{
    height: 25px;/*Espaço permitido*/
}

.header--logo img{
    height: 100%;/*100% da altura do espaço disponivel*/
}

.header--user{
    height: 35px;
}

.header--user img{
    height: 100%;
    border-radius: 3px;
}
