.movieRow{
    margin-bottom: 30px; /*Ajusta a margem de baixo de uma linha pra outra */
}
.movieRow h2{
    margin: 0px 0px 0px 30px; /*Ajusta a margem de baixo*/
}
.movieRow--listarea{
    overflow-x: hidden;/*Aqueles que ultrapasssarem o eixo X da tela serão cortados*/
    padding-left: 30px;
}
.movieRow--list{
    transition: all ease 0.3s;
}
.movieRow--item{
    display: inline-block;/*Organiza os elementos em uma linha só*/
    width: 150px;/*Define um tamanho fixo*/
    cursor: pointer;
}
.movieRow--item img{
    width: 100%;/*Utiliza 100% do tamanho fixo definido*/
    transform: scale(0.9);/*Reduz a escala dos elementos para 90%(0.9)*/
    transition: all ease 0.3s;/*A transição para o hover sera do tipo ease*/
}
.movieRow--item img:hover{/*Enquanto o mouse esta por cima da imagem*/
    transform: scale(1);/*A escala volta a 100%*/
}

.movieRow--left, .movieRow--right{
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.4s;
    border-radius: 10px;
}

.movieRow--left{
    left: 0;
}

.movieRow--right{
    right: 0;
}

.movieRow:hover .movieRow--left, .movieRow:hover .movieRow--right{
    opacity: 1;
}

@media (max-width:760px){
    .movieRow--left, .movieRow--right{
        opacity: 1;
    }
}